@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Bungee&family=Russo+One&family=Inter&display=swap');

html {
  overflow: hidden;
  font-size: 62.5%;
}

@media (max-height: 599px), (max-width: 600px) {
  html {
    overflow: visible;
  }
}

body {
  margin: 0;
  font-family: 'Josefin Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
